.popup__container {
    width: 430px;
    padding: 34px 36px 36px;
    background: #FFFFFF;
    position: relative;
    border-radius: 10px;
    box-sizing: border-box;
}

@media screen and (min-width: 320px) and (max-width: 600px) {
    .popup__container {
        width: 282px;
    }
}